
html {
  height: 100%;
}
body {
  font-family: Helvetica, sans-serif;
  height: 100%;
}
#wrap {
  background-color: #fbf9fe;
  min-height: 100%;
}
.fade-transition {
  -webkit-transition: opacity .3s ease;
  transition: opacity .3s ease;
}
.fade-enter, .fade-leave {
  opacity: 0;
}



